@media (prefers-color-scheme: dark) {
  :root {
    // --bg: #323f46;
    // --bg: #554e65;
    --bg: #0e141b;
    --bg-sel: #0e001c;
    --gradient: linear-gradient(-70deg, #a2facf 0%, #64acff 100%);
    --gradient-sel: #64acff;
    --link: #88c0d0;
    --code: #fff6ae;
    --font: #ffffff;
    --head: #f2f5f7;
    --highlight: #617bff66;
    --blend: lighten;
    --accent: #a6a1b0;
    --accent2: hsl(210deg, 38%, 15%);
    --filter: invert(100%);
    --button-icon: var(--icon-sun);
    --icon-filter: var(--icon-sun-filter);
  }
}
[data-theme="dark"] {
  // --bg: #323f46;
  // --bg: #554e65;
  --bg: #0e141b;
  --bg-sel: #0e001c;
  --highlight: #617bff66;
  --link: #88c0d0;
  --font: #ffffff;
  --head: #f2f5f7;
  --gradient-sel: #64acff;
  --code: #fff6ae;
  --gradient: linear-gradient(-70deg, #a2facf 0%, #64acff 100%);
  --blend: lighten;
  --filter: invert(100%);
  --accent: #a6a1b0;
  --accent2: hsl(210deg, 38%, 15%);
  --button-icon: var(--icon-sun);
  --icon-filter: var(--icon-sun-filter);
}
[data-theme="light"] {
  --filter: invert(0%);
  --blend: darken;
  --gradient-sel: slateblue;
  --gradient: linear-gradient(-70deg, #64acff 0%, slateblue 100%);
  --link: #458588;
  --code: #cd5555;
  --font: #252525;
  --head: #555;
  --accent: #a99f9a;
  --accent2: rgba(255, 135, 7, 0.17);
  --button-icon: var(--icon-moon);
  --highlight: #edbd7766;
  --icon-filter: var(--icon-moon-filter);
  // --bg: #ebe4e1;
  --bg: #fff1e5;
  --bg: #fff1e5;
  // --bg-sel: #ebe4e1;
}
* {
  @include box-sizing;
  line-height: 1.5;
  box-sizing: border-box;
  // -webkit-font-smoothing: antialiased;
  -webkit-font-smoothing: subpixel-antialiased;
}

//-----------
// Content
//-----------
html,
.content {
  display: block;
  color: var(--font);
  background: var(--bg);
  // margin-left: auto;
  // margin-right: auto;
  margin: 0;
  height: 100%;
  // padding-left: 12.5%;
  // max-width: 1400px;
}
html {
  color: var(--bg);
  font-family: $sans-serif;
  font-size: 22px;
  overflow: scroll;
  overflow-y: hidden;
  overflow-x: hidden;
  margin: 0;
  @media (max-width: 625px) {
    font-size: 20px;
  }
}
body {
  margin: 0;
  color: var(--font);
  transition: background 0.8s ease-in-out, color 0.5s ease;
  background: var(--bg);
  // overflow: hidden;
  -webkit-text-size-adjust: 100%;
}
.container {
  @media (min-width: 1100px) {
    // Tufte
    padding-right: 20%;
    width: 87.5%;
    max-width: 1250px;
  }

  margin: 0 auto;
  max-width: 1000px;
  width: 95%;
  transition: background 0.8s ease-in-out, color 0.5s ease;
}
h1 {
  color: var(--head);
  font-weight: normal;
  margin-top: 4rem;
  margin-bottom: 1.5rem;
  font-size: 3.2rem;
  line-height: 1;
}
h2 {
  font-family: "Fira Sans", sans-serif;
  color: var(--head);
  margin-top: 2.1rem;
  // text-align: right;
  border-bottom: 2px solid var(--accent);
  margin-bottom: 1.4rem;
  font-size: 1.8rem;
  line-height: 1;
}
h3 {
  color: var(--head);
  text-transform: uppercase;
  font-family: "Josefin Sans", sans-serif;
  font-size: 1.5rem;
  margin-bottom: 0.5rem;
}
h4,
h5,
h6 {
  color: var(--head);
  font-family: $sans-serif;
  line-height: normal;
}
a {
  color: var(--link);
  text-decoration: none;
}
blockquote {
  display: block;
  // margin-inline-end: 4rem;

  border-bottom: 3px double #727272;
  border-top: 3px double #727272;
}
img {
  display: block;
  margin: 0 0 1rem;
  filter: var(--filter);
  mix-blend-mode: var(--blend);
  max-width: 100%;

  &:hover,
  &:focus {
    // transform: scale(1.03);
    transition-duration: 0.5s;
    // border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  }
}
figcaption {
  font-style: italic;
  color: #aaa;
}
::-moz-selection {
  /* Code for Firefox */
  // color: var(--bg-sel);
  // background: var(--font);
  background: var(--highlight);
}
::selection {
  // color: var(--bg-sel);
  background: var(--highlight);
  // background: var(--font);
}

//-------------
// Tables
// ------------

// Taken from Practical Typography
thead {
  display: table-header-group;
  vertical-align: middle;
  border-color: inherit;
}
table {
  margin-bottom: 1.5em;
  border-collapse: collapse;
  width: 95%;
  margin-left: 0.75em;
}
table th {
  font-family: "Josefin Sans";
  font-weight: normal;
  text-transform: uppercase;
  font-size: 90%;
  font-style: bold;
  letter-spacing: 1px;
  padding: 0.3rem 0.5rem;
  line-height: 1.05;
}
tbody {
  display: table-row-group;
  vertical-align: middle;
  border-color: inherit;
}
tr {
  display: table-row;
  vertical-align: inherit;
  border-color: inherit;
}
table td {
  padding: 0.4em;
  text-align: center;
  hyphens: none;
}
table td + td {
  border-left: 1px solid var(--accent);
}
table tr + tr {
  border-top: 1px solid var(--accent);
}

// ----------------
// Custom Scrollbar
// ----------------
.custom-scrollbar {
  display: block;
  overflow: auto;
  height: 100vh;
}
.custom-scrollbar::-webkit-scrollbar {
  background-color: transparent;
  width: 18px;
}
.custom-scrollbar::-webkit-scrollbar-track {
  background-color: transparent;
}
.custom-scrollbar::-webkit-scrollbar-thumb {
  background: var(--accent);
  border-radius: 20px;
  border: 6px solid transparent;
  background-clip: content-box;
  // FIXME
  &:hover {
    opacity: 0.6;
  }
}
.custom-scrollbar::-webkit-scrollbar-corner {
  background: var(--bg);
}

// ----------------
// Dark Mode toggle
// ----------------
.toggle {
  display: none;
}
.label {
  display: inline-block;
  align-items: right;
  // justify-content: space-between;
  // position: relative;

  position: fixed;
  left: auto;
  bottom: auto;
  right: 2.5vw;
  top: 1.2rem;

  background: var(--font);
  border-radius: 5rem;
  padding: 0.5rem;
  margin: 1rem 0 0 1rem;
  height: 2rem;
  width: 2rem;
  font-size: 1.5rem;
  transition: background 0.5s ease;
  cursor: pointer;
  z-index: 1;

  @media only screen and (max-width: 850px) {
    transform: scale(0.92);
    top: 0.5rem;
  }

  @media only screen and (max-width: 625px) {
    transform: scale(0.85);
    top: 0.1rem;
  }
}

.themebox {
  display: inline-block;
  // border: 1px solid var(--gradient-sel);
  // border-radius: 5px;
  padding: 0 5px;
  // box-shadow: 0 2px 0 var(--gradient-sel);
  color: var(--font);
  transition: background 0.8s ease;

  // &:active {
  //   box-shadow: none;
  //   transform: translateY(2px);
  // }
}

.themebox::after {
  content: "Night";
}
.toggle:checked ~ .themebox::after {
  content: "Day";
}

.label .ball {
  position: absolute;
  background: var(--bg);
  border-radius: 50%;

  top: 0.25rem;
  left: 0.25rem;
  height: 1.5rem;
  width: 0.75rem;
  border-radius: 0.75rem 0 0 0.75rem;

  transition: transform 0.8s ease, background-color 0.6s ease;
}
.toggle:checked + .content .label .ball {
  display: inline-block;
  transform: translateX(0.75rem) rotate(180deg);
}

.toggle-icon {
  background: var(--button-icon);
  width: 1.2em;
  height: 1.2em;
  margin: 0 0 0 0.5em;
  transform: translateY(1px); /* Optical adjustment */
  filter: var(--icon-filter);
  color: var(--font);
  transition: filter 300ms ease-in-out;
}

.katex {
  font-size: 1em !important;
}

